import React, { Component } from 'react'
//react-redux 
import { connect } from 'react-redux';
//react
import { CSSTransition } from 'react-transition-group';
//actionCreators
import { actionCreators } from "./store"
//loginactionCreators
import { actionCreators as LoginactionCreators} from "../../pages/login/store"
//
import { Link } from "react-router-dom"
import {
  HeaderWrapper,
  Logo,
  Nav, 
  NavItem,
  SearchWrapper,
  SearchNav,
  SearchInfo,
	SearchInfoTitle,
	SearchInfoSwitch,
	SearchInfoList,
	SearchInfoItem,
  Addition,
  Button,

} from "./style.js"
 class index extends Component {
  getListArea() {
      const { focused , list ,  page , handleMouseEnter , handleMouseLeave , mouseIn , handleChangePage , totalPage} = this.props;
      const newList = list.toJS();
		const pageList = [];
		if (newList.length) {
			for (let i = (page - 1) * 10; i < page * 10; i++) {
				pageList.push(
					<SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
				)
			}
		};
		if (focused || mouseIn) {
			return (
				<SearchInfo 
					onMouseEnter={handleMouseEnter}
					onMouseLeave={handleMouseLeave}
				>
					<SearchInfoTitle>
						
                  <SearchInfoSwitch 
                  // 
							onClick={() => handleChangePage(page, totalPage, this.spinIcon)}
						>
							<i ref={(icon) => {this.spinIcon = icon}} className="iconfont spin">&#xe64e;</i>
							
						</SearchInfoSwitch>
					</SearchInfoTitle>
					<SearchInfoList>
						{pageList}
					</SearchInfoList>
				</SearchInfo>
			)
		}else {
			return null;
		}
	}
    render() {
   const {focused , handleInputFocus , handleInputBlur, list , login , loginout}  = this.props;
    return (
      <div>
        <HeaderWrapper>
          <Link to = "/">
          {/*  */}
            <Logo></Logo>
          </Link>
          
          <Nav>
            <NavItem className = "left active"></NavItem>
            <NavItem className = "left">APP</NavItem>
            {login ?  <NavItem className = "right" onClick = {() => {loginout()}} ></NavItem> :
                  <Link to = "/login">
                  {/*  */}
                  <NavItem className = "right"></NavItem>
                  </Link> 
           }
            <NavItem className = "right"><i className="iconfont">&#xe636;</i></NavItem>
            <SearchWrapper>
              <CSSTransition
                in={focused}
                timeout={200}
                //&.slide-enter
                classNames="slide"
              >
                 <SearchNav
                 className = {focused ? "focused" : ""}
                 onFocus = {() => { handleInputFocus(list) }}
                 onBlur = { handleInputBlur}
                 >
                 </SearchNav>
              </CSSTransition>
              <i className = {focused ? "focused iconfont zoom" : "iconfont zoom"}>&#xe64d;</i>
              {this.getListArea()}
            </SearchWrapper>  
          </Nav>
      
          <Addition>
            <Link to = "/write">
              <Button className = "writtig" > 
                <i className="iconfont">&#xe61c;</i>
                  
              </Button>
            </Link>
              <Button className = "reg"></Button>
          </Addition>
        </HeaderWrapper>
      </div>
    )
  }

}
const StatetoProps = (state) => {
   return {
     //focused : state.headerReducer.focused 
     //immutable focused : state.headerReducer.get("focused")
     // state js   immutable state.get("headerReducer").get("focused")
     //
      focused : state.getIn(["headerReducer" , "focused"]),
      list: state.getIn(['headerReducer', 'list']),
      page: state.getIn(['headerReducer', 'page']),
      totalPage: state.getIn(['headerReducer', 'totalPage']),
      mouseIn: state.getIn(['headerReducer', 'mouseIn']),
      //
      login : state.getIn(['LoginReducer', 'login']),
   }  
};

const DispathtoProps = (dispatch) => {
  return {
    handleInputFocus(list) {
        //
        if(list.size === 0){
        dispatch (actionCreators.getList());
        }
        dispatch (actionCreators.InputFocus());
    },

    handleInputBlur() {
      dispatch (actionCreators.InputBlur());
    },

    handleMouseEnter() {
       dispatch (actionCreators.MouseEnter())
    },

    handleMouseLeave() {
      dispatch (actionCreators.MouseLeave())
    },
    
    handleChangePage ( page , totalPage ,  spin) {
      //
      let originAngle = spin.style.transform.replace(/[^0-9]/ig, '');
			if (originAngle) {
        //
				originAngle = parseInt(originAngle, 10);
			}else {
				originAngle = 0;
			}
			spin.style.transform = 'rotate(' + (originAngle + 360) + 'deg)';

      //
       if(page < totalPage - 1){
         dispatch (actionCreators.ChangePage( page + 1 ))
       }else{
         dispatch (actionCreators.ChangePage( 1 ))
       }
      
    },
    //
    loginout() {
      dispatch(LoginactionCreators.logout())
    }
  }
}
export default connect(StatetoProps , DispathtoProps)(index)


